/* oswald-regular - latin_cyrillic */
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("./fonts/oswald-v48-latin_cyrillic-regular.eot"); /* IE9 Compat Modes */
  src: local(""),
    url("./fonts/oswald-v48-latin_cyrillic-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("./fonts/oswald-v48-latin_cyrillic-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("./fonts/oswald-v48-latin_cyrillic-regular.woff") format("woff"),
    /* Modern Browsers */ url("./fonts/oswald-v48-latin_cyrillic-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("./fonts/oswald-v48-latin_cyrillic-regular.svg#Oswald") format("svg"); /* Legacy iOS */
}
body {
  margin: 0;
  font-family: "Oswald";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
